<template>
  <el-container v-loading="loading">
    <div class="box">
      <h3>{{activeheader}}</h3>
      <div class="input-box">
        <span></span>
        <label for class="label require">医生名称：</label>
        <el-input class="input-width" v-model="name" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label class="label require">所在地区:</label>
        <user-address
          ref="addDocAddress"
          @countryChange="countryChange"
          :defaultValue="defaultAddress"
          seletStyle="width:216px"
        />
      </div>
      <div class="input-box">
        <label :class="{label: true, require: true, disabled: state}">所在医院:</label>
        <el-select v-model="hospitalId" placeholder="请选择" :disabled="state">
          <el-option v-for="item in hosName" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
        <span class="hosTip">{{hosTip}}</span>
      </div>
      <div class="input-box">
        <span></span>
        <label for class="label">所在科室：</label>
        <el-input class="input-width" v-model="dept" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label class="label require">标准科室:</label>
        <hos-offices ref="addDocOffices" :defaultValue="defaultOffices" seletStyle="width:216px"/>
      </div>
      <div class="input-box">
        <label class="label require">医生职称:</label>
        <el-select v-model="level" placeholder="请选择">
          <el-option
            v-for="item in doctorTitleList"
            :key="item.id"
            :label="item.dValue"
            :value="item.id"
          ></el-option>
        </el-select>
      </div>
      <div class="input-box">
        <span></span>
        <label class="label require">医生电话：</label>
        <el-input class="input-width" v-model="phone" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label class="label">上传照片</label>
        <div class="avatar-uploader" v-if="!imageUrl">
          <div tabindex="0" class="el-upload el-upload--text">
            <label for="upPhoto" class="el-icon-plus avatar-uploader-icon"></label>
            <input
              @change="fileChange"
              type="file"
              name="file"
              class="el-upload__input"
              id="upPhoto"
            >
          </div>
        </div>
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <span v-if="imageUrl" class="photoDel" @click="delPhoto">删除</span>
      </div>
      <div class="input-box">
        <label class="label">医生简介：</label>
        <el-input type="textarea" :rows="2" size="medium" placeholder="请输入内容" v-model="info"></el-input>
      </div>
      <div class="input-box">
        <label class="label">医生擅长：</label>
        <el-input type="textarea" :rows="2" size="medium" placeholder="请输入内容" v-model="skill"></el-input>
      </div>
      <div class="input-box">
        <label class="label">备注：</label>
        <el-input type="textarea" :rows="2" size="medium" placeholder="请输入内容" v-model="remark"></el-input>
      </div>
      <div class="input-box box-last">
        <el-button type="primary" @click="submit">提交</el-button>
        <el-button @click="cancle">取消</el-button>
      </div>
    </div>
  </el-container>
</template>

<script>
import { doctorTitleList } from "data/listBody";
import { FileChange } from "utils/formData";
import check from "utils/check";
import UserAddress from "components/address";
import HosOffices from "components/hosOffices";
import queryString from "querystring";

let headers = ["新增医生", "修改医生信息"];
let hosTips = ["请先选择地区", "该地区没有对应医院信息，请先新建医院"];
let requireArray = []; //必传字段

export default {
  name: "addDoc",
  data() {
    return {
      name: "",
      dept: "",
      hospitalId: "",
      level: "",
      imageUrl: "",
      phone: "",
      info: "",
      skill: "",
      remark: "",
      loading: false,
      id: "",
      hosTip: hosTips[0],
      state: true, //医院是否可选
      defaultAddress: null, //默认地址
      defaultOffices: null, //默认科室
      activeheader: headers[0],
      check: check.methods.check,
      checkTip: check.tips.checkTip,
      doctorTitleList,
      hosName: []
    };
  },
  components: {
    UserAddress,
    HosOffices
  },
  mounted() {
    this.editInit();
  },
  methods: {
    fileChange(e) {
      this.loading = true;
      FileChange(e, res => {
        this.loading = false;
        this.imageUrl = res.url;
      });
    },
    delPhoto() {
      this.imageUrl = "";
    },
    editInit() {
      let { type, id } = this.$route.query;
      this.api
        .get(`/hospital`)
        .then(res => {
          this.hosName = res.data.list;
          this.hospitalId = "";
          if (this.hosName.length < 1) {
            this.state = true;
            return (this.hosTip = hosTips[1]);
          }
          this.state = false;
          this.hosTip = "";
        })
        .catch(err => {
          console.log(err);
          this.$message(err.message);
        });
      if (type === "edit") this.activeheader = headers[1];
      if (!id) return;
      this.loading = true;
      this.api
        .get(`/doctor/${id}`)
        .then(res => {
          //查询产品详情
          this.dataInit(res.data);
          setTimeout(() => {
            this.loading = false;
          }, 20);
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.message);
        });
    },
    dataInit(data) {
      data = data || {};
      let {
        name,
        hospitalName,
        dept,
        dept1,
        dept2,
        headIcon,
        hospitalId,
        level,
        phone,
        skill,
        remark,
        info,
        id,
        province,
        city,
        country
      } = data;
      this.name = name;
      this.hospitalName = hospitalName;
      this.dept = dept;
      this.phone = phone;
      this.skill = skill;
      this.remark = remark;
      this.info = info;
      this.id = id;
      this.imageUrl = headIcon;
      // 医生职称处理
      let docArr = doctorTitleList.filter(item => {
        return item.id == level;
      });
      if (docArr.length > 0) {
        this.level = docArr[0].dValue;
      }

      if (hospitalId) {
        setTimeout(() => {
          this.hospitalId = hospitalId;
          this.hosTip = "";
        }, 1000);
      }
      this.defaultAddress = {
        province,
        city,
        country
      };
      this.defaultOffices = {
        dept1,
        dept2
      };
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    countryChange(params) {
      //   let param = queryString.stringify(params);
      //   this.api.get(`/hospital?${param}`).then((res)=>{
      //     this.hosName = res.data.list;
      //     this.hospitalId='';
      //     if(this.hosName.length < 1){
      //       this.state = true;
      //       return this.hosTip = hosTips[1];
      //     }
      //     this.state = false;
      //     this.hosTip = '';
      //   }).catch((err) => {
      //     console.log(err)
      //     this.$message(err.message)
      //   })
    },
    submit() {
      let addressParams = this.$refs.addDocAddress.getData();
      let deptParams = this.$refs.addDocOffices.getData();
      let params = {
        name: this.name,
        dept: this.dept,
        hospitalId: this.hospitalId,
        level: this.level,
        phone: this.phone,
        headIcon: this.imageUrl,
        info: this.info,
        remark: this.remark,
        skill: this.skill
      };
      let param = this.lodash.assign({}, params, addressParams, deptParams);
      this.check(param, requireArray)
        .then(
          () => {
            this.addUser(param);
          },
          () => {
            this.$message(this.checkTip);
          }
        )
        .catch(err => {
          console.log(err);
          this.$message(err.data.message);
        });
    },
    editUser(param) {
      //修改用户
      param.id = this.id;
      this.api
        .put("/doctor", param, "form")
        .then(res => {
          this.$router.go(-1);
        })
        .catch(err => {
          console.log(err);
          this.$message(err.data.message);
        });
    },
    addUser(param) {
      //添加用户
      if (this.$route.query.type === "edit") return this.editUser(param);
      this.api
        .post("/doctor", param, "form")
        .then(res => {
          this.$router.go(-1);
        })
        .catch(err => {
          console.log(err);
          this.$message(err.data.message);
        });
    },
    cancle() {
      this.$router.go(-1);
    }
  }
};
</script>
 
